.contacts {
  padding: 60px 0 104px;

  @include vp-1199 {
    padding: 80px 0 70px;
  }

  @include vp-767 {
    padding: 60px 0 30px;
  }
}

.contacts .container {
  display: flex;
  justify-content: space-between;
  gap: 30px;

  @include vp-1199 {
    flex-direction: column;
    gap: 37px;
  }

  @include vp-767 {
    gap: 31px;
  }
}

.contacts__text-wrapper {
  display: flex;
  flex-direction: column;
  max-width: 483px;

  @include vp-1199 {
    max-width: 100%;
  }
}

.contacts__title-wrapper {
  margin: 0 0 23px;
  position: relative;

  @include vp-1199 {
    margin: 0 0 18px;
  }

  @include vp-767 {
    margin: 0 0 11px;
  }
}

.contacts__title {
  font-size: 35px;
  font-weight: 800;
  margin: 0;
  padding: 23px 40px 0 0;

  @include vp-1199 {
    font-size: 25px;
    padding: 8px 40px 0 0;
  }

  @include vp-767 {
    font-size: 22px;
  }
}

.contacts__title-shadow {
  font-size: 55px;
  font-weight: 800;
  opacity: 0.03;
  position: absolute;
  top: 6px;
  left: 0;

  @include vp-1199 {
    font-size: 40px;
  }

  @include vp-767 {
    font-size: 26px;
  }
}

.contacts__desc {
  line-height: 1.5;
  margin: 0 0 50px;

  @include vp-1199 {
    margin: 0 0 40px;
    max-width: 71%;
  }

  @include vp-767 {
    margin: 0 0 33px;
    max-width: 95%;
    font-size: 14px;
    line-height: 1.4;
  }
}


.contacts__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 33px;

  @include vp-1199 {
    flex-direction: row;
    gap: 44px;
  }

  @include vp-767 {
    flex-direction: column;
    gap: 27px;
  }
}

.contacts__item {
  display: flex;
  gap: 30px;

  @include vp-1199 {
    gap: 10px;
  }
}

.contacts__item-svg {
  align-self: center;
  fill: $color-primary-black;

  @include vp-1199 {
    align-self: flex-start;
    width: 24px;
    height: 24px;
  }
}

.contacts__item-text {
  display: flex;
  flex-direction: column;
  gap: 7px;

  @include vp-1199 {
    gap: 6px;
  }

  @include vp-767 {
    gap: 8px;
  }
}

.contacts__item-title {
  font-size: 18px;
  font-weight: 600;
  margin: 0;

  @include vp-1199 {
    font-size: 16px;
  }

  @include vp-767 {
    font-size: 14px;
  }
}

.contacts__item-link {
  color: inherit;
  display: block;
  text-decoration: none;
  line-height: 1.6;
  transition: opacity $trans-default;

  @include hover-focus {
    opacity: 0.7;
  }

  @include active {
    opacity: 0.5;
  }

  @include vp-1199 {
    font-size: 14px;
  }
}

.contacts__item-address {
  font-style: normal;
  line-height: 1.6;

  @include vp-1199 {
    font-size: 14px;
  }

  @include vp-767 {
    line-height: 1.4;
    max-width: 74%;
  }
}

.contacts__map {
  width: 585px;
  height: 324px;
  align-self: flex-end;
  background-color: $color-default-white-20;

  @include vp-1199 {
    width: 100%;
    height: 340px;
  }

  @include vp-767 {
    height: 320px;
  }
}
